interface Props {
  children?: ReactNode
};

const CommonPropTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.arrayOf(PropTypes.element)]),
}


interface MenuProps {
  open?: boolean,
  children?: ReactNode,
  items?: MenuItemProps[],
};

const Menu = ({children, open = true, items = []}: MenuProps) => {
  const visibleClass=open? 'visible': '';

  return <ul className={`metismenu-container ${visibleClass}`}>
    {children}
    {items && items.length > 0 && items.map(item => <MenuItem key={item.key || item.name} name={item.name} link={item.link || '#'} icon={item.icon} items={item.items}></MenuItem>)}
  </ul>
}

interface MenuItemProps {
  name: string,
  key?: string,
  icon?: string,
  open?: boolean,
  link?: string,
  active?: boolean,
  children?: ReactNode,
  items?: MenuItemProps[],
};

const MenuItem = ({name, icon='', open = true, children, link = '#', active = false, items = []}: MenuItemProps) => {
  const [getOpen, setOpen]=useState(open);

  const arrowClass=getOpen? 'rotate-minus-90': '';
  const activeClass=active? 'has-active-child': '';
  const showArrow=children || (items && items.length > 0);

  return <li className="metismenu-item">
    <a href={link} className={`metismenu-link ${activeClass}`} onClick={() => setOpen(!getOpen)}>
      <i className={`metismenu-icon ${icon}`}></i>
      {name}
      {showArrow && <i className={`metismenu-state-icon pe-7s-angle-down caret-left ${arrowClass}`}></i>}
    </a>
    {children}
    {items && items.length > 0 && <Menu items={items} open={getOpen}></Menu>}
  </li>
}


