//@ts-ignore
const Architect = ({ pageTitle, children, openSidebar = true }) => {
  var userInfo = {
    name: 'M Sheriff',
  };

  const [getOpenSidebar, setOpenSidebar] = useState(openSidebar);
  const toggleSidebar = useCallback(() => setOpenSidebar(!getOpenSidebar), [getOpenSidebar]);

  once('toggleSidebar', toggleSidebar);

  return (
    <div className={`app-container app-theme-white fixed-header fixed-sidebar fixed-footer ${getOpenSidebar? '': 'closed-sidebar'}`}>
      <AppHeader userInfo={userInfo} />
      <main className="app-main">
        <AppSidebar></AppSidebar>
        <div className="app-main__outer">
          <div className="app-main__inner">
            <PageTitle pageTitle={pageTitle} />
            {children}
          </div>
        </div>
      </main>
    </div>
  )
}

export default Architect;
