// events.js

function on(eventName: string, listener: any) {
    if (typeof document !== `undefined`) {
        document.addEventListener(eventName, listener);
    } else {
        // There are no interactive events for server (SSR), so we simply no-op.
    }
}

function off(eventName: string, listener: any) {
    if (typeof document !== `undefined`) {
        document.removeEventListener(eventName, listener);
    } else {
        // There are no interactive events for server (SSR), so we simply no-op.
    }
}

function once(eventName: string, listener: any) {
    on(eventName, handleEventOnce);

    function handleEventOnce(event: any) {
        listener(event);
        off(eventName, handleEventOnce);
    }
}

function trigger(eventName: string, data: any = null) {
    if (typeof document !== `undefined`) {
        const event = new CustomEvent(eventName, { detail: data });
        document.dispatchEvent(event);
    } else {
        // There are no interactive events for server (SSR), so we simply no-op.
    }
}

// aliases
const subscribe = on;
const unsubscribe = off;
const publish = trigger;
const dispatch = publish;

export {
    on, once, off, trigger,
    subscribe, unsubscribe, publish, dispatch
};
